<template>
  <div class="news-wrap">
    <div class="header">
      <div class="time">
        <span v-if="dataSource.status === 0" class="wait">{{
          $t('admin.reviewing')
        }}</span>
        <span v-else-if="dataSource.status === 2" class="fail">{{
          $t('admin.reviewFail')
        }}</span>
        {{ dataSource.updateTime || dataSource.createTime  || '00:00:00' }}
      </div>
      <div class="num">
        {{ dataSource.readingVolume || 0 }}{{ $t('admin.read') }}
      </div>
    </div>
    <div class="bottom">
      <div class="news-content" v-if="active === 0">
        <div class="content">
          {{ dataSource.content }}
        </div>
        <div class="img-box" v-if="dataSource.preview">
          <img :src="dataSource.preview" alt="" />
        </div>
      </div>
      <div class="article-content" v-else>
        <div class="img-box">
          <img :src="dataSource.preview" alt="" />
        </div>
        <div class="text" v-html="dataSource.content"></div>
      </div>
      <div class="button-group">
        <van-button class="modify-btn" @click="handleToEdit" color="#f8bb5a">{{
          $t('admin.modify')
        }}</van-button>
        <van-button class="delete-btn" @click="handleDelete" color="#dbd9d5">{{
          $t('admin.delete')
        }}</van-button>
      </div>
    </div>
  </div>
</template>

<script>
import { handleDeleteNews } from '@/services/news'
export default {
  name: '',
  props: ['dataSource', 'active'],
  data () {
    return {

    }
  },
  methods: {
    async handleDelete () {
      const resp = await handleDeleteNews(this.dataSource.id)
      if (!resp.success) return
      this.$toast('Success')
      this.$parent.getList()
    },
    handleToEdit () {
      if (this.active === 0) {
        this.$router.push({
          path: `/ReleaseNews?id=${this.dataSource.id}`
        })
      } else {
        this.$router.push({
          path: `/ReleaseArticle?id=${this.dataSource.id}`
        })
      }
    }
  },
  mounted () {

  }
}
</script>

