<template>
  <div id="header">
    <div class="title">
      <div class="info">
        <div class="avatar">
          <img :src="mediaInfo.logo || defaultImg" alt="" translate="no" />
        </div>
        <div class="name" translate="no">{{ mediaInfo.nickname }}</div>
      </div>
      <div class="modify" @click="handleNameShow">
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#a-shuxiebianxie1"></use>
        </svg>
        <span v-if="mediaInfo.nicknameReviewStatus === 0">{{
          $t('admin.peddingReview')
        }}</span>
      </div>
    </div>
    <div class="data">
      <div class="data-item">
        <div class="value" translate="no">
          {{ mediaInfo.readingsCumulative || 0 }}
        </div>
        <div class="name">{{ $t('admin.readCount') }}</div>
      </div>
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#star1"></use>
      </svg>
      <div class="data-item">
        <div class="value" translate="no">
          {{ mediaInfo.readsNewsletter || 0 }}
        </div>
        <div class="name">{{ $t('admin.newsCount') }}</div>
      </div>
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#star1"></use>
      </svg>
      <div class="data-item">
        <div class="value" translate="no">
          {{ mediaInfo.articlesNumber || 0 }}
        </div>
        <div class="name">{{ $t('admin.articleCount') }}</div>
      </div>
    </div>
    <NamePop v-if="nameShow" @close="handleNameShow"></NamePop>
  </div>
</template>

<script>
import { handleMediaInfo } from '@/services/news'
import NamePop from './NamePop.vue'
export default {
  name: '',
  data () {
    return {
      nameShow: false,
      mediaInfo: {},
      defaultImg: require('../../../assets/logo.png')
    }
  },
  methods: {
    handleNameShow () {
      if (this.mediaInfo.nicknameReviewStatus === 0) return
      this.nameShow = !this.nameShow
    },
    async getInfo () {
      const resp = await handleMediaInfo()
      if (!resp.success) return
      this.mediaInfo = resp.data
    }
  },
  mounted () {
    this.getInfo()
  },
  components: { NamePop }
}
</script>

