<template>
  <div class="media-wrap">
    <MediaAdminHeader></MediaAdminHeader>
    <div id="content">
      <div class="name">
        <span>{{ $t('admin.contentAdmin') }}</span>
        <svg class="icon" aria-hidden="true" @click="handleRealese">
          <use xlink:href="#fabu"></use>
        </svg>
      </div>
      <div class="tab">
        <div
          class="tab-item"
          :class="{ 'tab-active': tabActive === 0 }"
          @click="handleTab(0)"
        >
          {{ $t('admin.news') }}
        </div>
        <div
          class="tab-item"
          :class="{ 'tab-active': tabActive === 1 }"
          @click="handleTab(1)"
        >
          {{ $t('admin.article') }}
        </div>
      </div>
      <div class="list" v-if="newsList.length > 0">
        <NewsletterItem
          v-for="item in newsList"
          :key="item.id"
          :active="tabActive"
          :dataSource="item"
        ></NewsletterItem>
      </div>
      <div class="no-data" v-else>{{ $t('common.noData') }}</div>
    </div>
    <ReleasePop v-if="releaseShow" @close="handleRealese"></ReleasePop>
  </div>
</template>

<script>
import { handleAdminNewsList } from '@/services/news'
import NewsletterItem from './components/NewsletterItem.vue'
import MediaAdminHeader from './components/MediaAdminHeader.vue'
import ReleasePop from './components/ReleasePop.vue'
export default {
  name: '',
  data () {
    return {
      tabActive: 0, // 0快讯 1文章
      releaseShow: false,
      newsList: []
    }
  },
  methods: {
    handleTab (index) {
      this.tabActive = index
      this.newsList = []
      this.getList()
    },
    handleRealese () {
      this.releaseShow = !this.releaseShow
    },
    async getList () {
      const resp = await handleAdminNewsList({
        type: this.tabActive
      })
      if (!resp.success) return
      this.newsList = resp.rows
    }
  },
  mounted () {
    this.getList()
  },
  components: { NewsletterItem, MediaAdminHeader, ReleasePop }
}
</script>

