<template>
  <CommomPop @close="closePop">
    <div class="name">{{ $t('admin.modifyInfo') }}</div>
    <div class="set-image">
      <van-uploader :after-read="afterRead" v-model="fileList">
        <div class="upload"><van-icon name="plus" size="25" /></div>
      </van-uploader>
    </div>
    <div class="input-wrap">
      <van-field
        translate="no"
        class="input"
        :placeholder="$t('common.inputName')"
        input-align="center"
        v-model="nickname"
      />
    </div>
    <div class="button-group">
      <div class="button-item" @click="handleConfirm">
        {{ $t('common.confirm') }}
      </div>
    </div>
  </CommomPop>
</template>

<script>
import { handleNickname } from '@/services/user'
import CommomPop from '@/components/common/CommomPop.vue'
import config from '@/config/app.config'
import { upload } from '@/services/upload'
export default {
  name: '',
  data () {
    return {
      nickname: '',
      logo: '',
      fileList: []
    }
  },
  methods: {
    closePop () {
      this.$emit('close')
    },
    // 图片上传
    async afterRead (file) {
      // 此时可以自行将文件上传至服务器
      var formdata = new FormData()
      formdata.append('file', file.file)
      const resp = await upload(formdata)
      if (!resp.success) {
        this.fileList = []
        return
      }
      this.logo = config.imgUrl + resp.data.url
    },
    async handleConfirm () {
      if (!this.nickname || !this.logo) {
        return this.$toast(this.$t('common.fill'))
      }
      const resp = await handleNickname({
        nickname: this.nickname,
        logo: this.logo
      })
      if (!resp.success) return
      this.$parent.getInfo()
      this.closePop()
    }
  },
  mounted () {

  },
  components: { CommomPop }
}
</script>

